@import "colors";
@import "./mobile";
@import "./fonts";

.logo-text {
  font-size: 32px;
  display: inline;
}

.DisappearI {
  object-fit: cover;
  border-radius: 50%;
  margin-right: 1rem;

  @include mobile {
    display: none;
  }
}
.logoutCompanyBtn {
  outline: none;
  border: none;
  background: none;
  &:focus {
    background: none;
  }
}
.logoutFreelancerBtn {
  outline: none;
  border: none;
  background: none;
  &:focus {
    background: none;
  }
}

.navBarLogo {
  color: yellowgreen;

  :hover {
    color: red;
  }
}

